00<template>
  <div id="customized-storeList">
    <div class="store-list">
      <div class="list-item">
        <div class="item-img fl-left">
          <img src="../../assets/pic.png">
          <div class="item-count" v-if="false">3</div>
        </div>
        <div class="item-info fl-left">
          <h4>巴黎欧莱雅官方旗舰店</h4>
          <p>定制商品数：2560</p>
        </div>
      </div>
      <div class="list-item">
        <div class="item-img fl-left">
          <img src="../../assets/pic.png">
          <div class="item-count">3</div>
        </div>
        <div class="item-info fl-left">
          <h4>巴黎欧莱雅官方旗舰店</h4>
          <p>定制商品数：2560</p>
        </div>
      </div>
      <div class="list-item">
        <div class="item-img fl-left">
          <img src="../../assets/pic.png">
          <div class="item-count">13</div>
        </div>
        <div class="item-info fl-left">
          <h4>巴黎欧莱雅官方旗舰店</h4>
          <p>定制商品数：2560</p>
        </div>
      </div>

    </div>
  </div>
</template>

<script>
  export default {
    data () {
      return {}
    },
    watch: {},
    methods: {}
  }
</script>

<style lang="scss">
  @import "../../sass/baseSass.scss";
  #customized-storeList{
    .store-list{
      background: #fff;

      .list-item{
        width: box(750);
        height: box(220);
        border-bottom: solid #ccc box(1);
        padding: box(30);



        .item-img{
          width: box(220);
          height: box(160);
          position: relative;

          img{
            width: box(220);
            height: box(160);
            border-radius: box(8);
          }
          .item-count{
            min-width: box(30);
            height: box(30);
            background-color: #fe4436;
            box-shadow: 1px 2px 7px 0px rgba(0, 0, 0, 0.35);
            border-radius: box(15);
            position: absolute;
            bottom: box(10);
            right: box(10);
            color: #fff;
            line-height: box(30);
            text-align: center;
            font-size: box(24);
            padding: 0 box(9);
          }
        }

        .item-info{
          width: box(440);
          height: box(160);
          position: relative;
          margin-left: box(30);
          color: #1a1a1a;

          h4{
            font-size: box(30);
          }
          p{
            font-size: box(28);
            position: absolute;
            bottom: 0;
            left: 0;
          }
        }


      }
    }
  }
</style>



